import React from 'react';
import styled from 'styled-components';
import searchTerm from '../util/highlight';
import { go, pipe, log } from '../util/fx';
import _ from 'lodash';

const Wrapper = styled.span`
  font-size: 1rem;
`;

const Highlight = styled.a`
  font-size: 1rem;
  background: ${props => props.theme.highlightColor};
  color: ${props => props.theme.fontColor};
`;

const NormalText = styled.span`
  font-size: 1rem;
`;

//pre 
export default ({ text }) => {
  const terms = _.flatMap(searchTerm);
  const chunks = _.split(text, ' ');

  const makeHighlight = _.chain(chunks)
    .map(chunk =>
      _.indexOf(terms, chunk) > -1 ? (
        <Highlight>{chunk}</Highlight>
      ) : (
        <NormalText>{chunk}</NormalText>
      )
    )
    .value();

  return <Wrapper>{makeHighlight}</Wrapper>;
};

