export const log = console.log;
const _curry = f => (a, ..._) => (_.length ? f(a, ..._) : (..._) => f(a, ..._));
const _isPromise = (a, f) => (a instanceof Promise ? a.then(f) : f(a));
const _nop = Symbol('nop');
const _isNop = (acc, a, f) =>
  a instanceof Promise
    ? a.then(
        a => f(acc, a),
        e => (e === _nop ? acc : Promise.reject(e))
      )
    : f(acc, a);
const _head = iter => _isPromise(_take(1, iter), ([h]) => h);
const _take = _curry((l, iterable) => {
  let res = [];
  const iterator = iterable[Symbol.iterator]();
  let cur;
  return (function recur() {
    while (!(cur = iterator.next()).done) {
      const a = cur.value;
      if (a instanceof Promise)
        return a
          .then(a => {
            res.push(a);
            return res.length === l ? res : recur();
          })
          .catch(e => (e === _nop ? recur() : Promise.reject(e)));
      res.push(a);
      if (res.length === l) return res;
    }
    return res;
  })();
});
const _reduce = _curry((f, acc, iter) => {
  if (!iter) return _reduce(f, _head((iter = acc[Symbol.iterator]())), iter);

  iter = iter[Symbol.iterator]();

  return _isPromise(acc, function recur(acc) {
    let cur;
    while (!(cur = iter.next()).done) {
      const a = cur.value;
      acc = _isNop(acc, cur.value, f);
      if (acc instanceof Promise) return acc.then(recur);
    }
    return acc;
  });
});

export const go = (...args) => _reduce((a, f) => f(a), args);
export const pipe = (f, ...fs) => (...as) => go(f(...as), ...fs);
